<template>
  <v-row class="ma-1">
    <v-col
      v-for="fighterDesign in fighterDesigns"
      :key="fighterDesign.fileName"
      class="d-flex child-flex ma-0 pa-0"
      cols="6"
    >
      <v-img
        :src="assetPath(fighterDesign.image)"
        aspect-ratio="1"
        class="grey lighten-2"
        style="margin: 3px"
        @click="onClick(fighterDesign.fileName)"
      >
        <template v-slot:placeholder>
          <v-row class="fill-height ma-0" align="center" justify="center">
            <v-progress-circular
              indeterminate
              color="grey lighten-5"
            ></v-progress-circular>
          </v-row>
        </template>
      </v-img>
    </v-col>
  </v-row>
</template>

<script>
import { fighterDesigns } from "@/data/fighterDesigns.js";

export default {
  data() {
    return {
      fighterDesigns,
    };
  },
  methods: {
    assetPath(image) {
      var images = require.context("@/assets/gallery", false);
      return images("./" + image);
    },
    onClick(fighter) {
      this.$router.push(`/fighters/${fighter}`);
    },
  },
};
</script>
