      <v-row justify="center">
        <!-- Editor dialog -->
        <v-dialog
          v-if="editorDialog && !overlay"
          v-model="editorDialog"
          scrollable
          width="auto"
          persistent
          :fullscreen="$vuetify.breakpoint.xsOnly && isLoggedIn()"
        >
          <v-card>
            <v-divider></v-divider>
            <Editor
              v-if="isLoggedIn()"
              class="container"
              :analytics="analytics"
              :fighter="fighter"
              style="background-color: white"
            />

            <div v-if="!isLoggedIn()" style="padding: 20px">
              Welcome to Origami-Skin!
            </div>

            <v-divider></v-divider>

            <v-card-actions class="justify-end" style="background-color: white">
              <div style="display: flex">
                <v-btn
                  v-if="!isLoggedIn()"
                  variant="outlined"
                  style="margin-right: 10px"
                  @click="onClickLoginBtn()"
                  >Login</v-btn
                >
                <v-btn variant="outlined" @click="$router.replace('/')"
                  >Close</v-btn
                >
              </div>
