          </v-card>
        </v-dialog>

        <!-- Privacy dialog -->
        <v-dialog
          v-if="privacyDialog && !overlay"
          v-model="privacyDialog"
          scrollable
          width="auto"
          persistent
          :fullscreen="$vuetify.breakpoint.xsOnly && isLoggedIn()"
        >
          <v-card>
            <h2 class="dialogTitle">Privacy policy</h2>
            <v-divider></v-divider>
            <Privacy />
            <v-divider></v-divider>

            <v-card-actions class="justify-end" style="background-color: white">
              <div style="display: flex">
                <v-btn variant="outlined" @click="$router.replace('/')"
                  >Close</v-btn
