<script>
"use strict";

import Editor from "@/components/Editor";
import Gallery from "@/components/Gallery";
import Privacy from "@/components/Privacy";
// import Disqus from "@/components/Disqus";
import { findFighterDesign } from "@/data/fighterDesigns.js";
import * as firebase from "@/firebase.js";

export default {
  name: "App",

  components: {
    Editor,
    Gallery,
    Privacy,
    // Disqus,
  },

  data() {
    return {
      analytics: firebase.analytics,
      editorDialog: false,
      privacyDialog: false,
      fighter: null,
      authInfo: null,
      overlay: false,
      userId: null,
    };
  },
  mounted() {
    firebase.addLoginCallback(this.onChangefirebaseInfo);
    this.onChangeRoute(this.$route);
  },
  methods: {
    onChangefirebaseInfo(authInfo) {
      this.authInfo = authInfo;
      if (authInfo == null) {
        console.log("onChangefirebaseInfo(null)");
      } else {
        console.log(
          `onChangefirebaseInfo(${JSON.stringify(authInfo.user.uid)})`
        );
        firebase.setUser(authInfo.user.uid);
      }
      this.overlay = false;
    },
    isLoggedIn() {
      // return this.authInfo != null;
      return true;
    },
    loginBtnStr() {
      if (this.isLoggedIn()) {
        const name = this.authInfo.user.displayName;
        return `logout ${name}`;
      } else {
        return "login";
      }
    },
    onClickLoginBtn() {
      if (this.isLoggedIn()) {
        firebase.logout();
      } else {
        firebase.login();
      }
    },
    logoPath() {
      var images = require.context("@/assets", false);
      return images("./logo.png");
    },
    openEditorDialog(fighter) {
      this.fighter = fighter;
      this.editorDialog = true;
    },
    closeEditorDialog() {
      this.fighter = null;
      this.editorDialog = false;
    },
    openPrivacyDialog() {
      this.privacyDialog = true;
    },
    closePrivacyDialog() {
      this.privacyDialog = false;
    },
    getFighter(route) {
      if (route === undefined) {
        return null;
      } else {
        const fighter = route.params.fighter;
        const design = findFighterDesign(fighter);
        if (design == null) {
          return null;
        }
        return fighter;
      }
    },
    onChangeRoute(route) {
      if (route.path === "/privacy") {
        this.openPrivacyDialog();
      } else if (route.path.startsWith("/fighters/")) {
        const fighter = this.getFighter(route);

        // if (fighter === "draken_03") {
        //   window.history.back();
        //   window.location.href = "http://origami-skin.imweb.me/?idx=7";
        // } else if (fighter === "draken_04") {
        //   window.history.back();
        //   window.location.href = "http://origami-skin.imweb.me/?idx=8";
        // } else if (fighter === "draken_01") {
        //   window.history.back();
        //   window.location.href = "http://origami-skin.imweb.me/?idx=9";
        // } else if (fighter == null) {
        //   this.closeEditorDialog();
        // } else {
        //   this.openEditorDialog(fighter);
        // }

        if (fighter == null) {
          this.closeEditorDialog();
        } else {
          this.openEditorDialog(fighter);
        }
      } else {
        this.closeEditorDialog();
        this.closePrivacyDialog();
      }
    },
  },
  watch: {
    $route(route) {
      this.onChangeRoute(route);
    },
  },
};
</script>
